@import url(https://fonts.googleapis.com/css2?family=Lemonada:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to left, rgb(47, 141, 248), rgb(178, 224, 250));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

img {
    height: 150px;
    width: 100%
}
.allCards {
    display: inline-block;
}
.Card {
    height: 200px;
    width: 200px;
    text-align: center;
    border-radius: 5px;
    margin: 3px;
    background: rgb(177, 225, 247);
    box-shadow: 0 0 5px 0.5px;
    padding: 15px;
}
.Card:hover {
    cursor: pointer;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: 0.8s;
}
input {
    padding: 10px 15px;
    border-radius: 3px;
    margin-top: -20px;
    margin-bottom: 20px;
    background: rgb(177, 225, 247);
    font-style: italic;
}
div {
    text-align: center;
}
.naslov {
    padding: 0px;

}
h1 {
    color: white;
    font-size: 70px;
    font-family: 'Lemonada', cursive;
    margin-top: -15px;
    margin-bottom: -10px;
}
.naslovM {
    color: white;
    font-weight: 500;
    margin-top: -25px;
    font-style: italic;
    font-size: 25px;
}
.autor {
    color: white;
}

