img {
    height: 150px;
    width: 100%
}
.allCards {
    display: inline-block;
}
.Card {
    height: 200px;
    width: 200px;
    text-align: center;
    border-radius: 5px;
    margin: 3px;
    background: rgb(177, 225, 247);
    box-shadow: 0 0 5px 0.5px;
    padding: 15px;
}
.Card:hover {
    cursor: pointer;
    transform: scale(1.1);
    transition: 0.8s;
}